import navHeader from '../common/nav-header'
export default {
  data () {
    return {
      currentSid: this.sid
    }
  },
  props: ['subCategory', 'sid'],
  components: {
    navHeader
  },
  methods: {
    changeSubCategory (o) {
      this.$emit('change-sub-category', o)
      this.currentSid = o.id
    }
  }
}



